.headerOption {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  color: gray;
  cursor: pointer;
}

.headerOption__title {
  font-size: 12px;
  font-weight: 400;
}

.headerOption__icon {
  object-fit: contain;
  height: 25px !important;
  width: 25px !important;
}

.headerOption:hover {
  color: black;
}

@media screen and (max-width: 650px) {
  .headerOption__title {
    font-size: 8px;
    font-weight: 400;
  }
}
